<template>
  <div class="home-header" :style="{backgroundColor: backgroundColor}">
    <div class="cont">
      <div class="logo" @click="logoCilck" >
        <img src="@/assets/avatar.png" alt=""/>
        <span class="name">{{ Author }}</span>
      </div>
      <div class="nav">
        <div v-for="item in headerList" :key="item.id" class="col" @click="navigateTo(item)">
          {{ item.title }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import appConfig from '@/config/app.json'
export default {
  name: 'HomeHeader',
  // inheritAttrs: false, // 避免传入的未声明的值绑定到子组件的根元素上
  props: {
    backgroundColor: {
      type: String,
      default: '#e0e0e0'
    }
  },
  data () {
    return {
      Author: appConfig.Author,
      headerList: appConfig.HomeHeader.nav
    }
  },

  mounted () {
  },

  methods: {
    logoCilck () {
      this.$emit('toHome')
    },
    navigateTo (item) {
      if (item.title === '首页' && this.$route.path !== '/home') {
        sessionStorage.setItem('headBg', 'transparent')
        this.$router.push('/home')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.home-header {
  z-index: 10;
  box-sizing: border-box;
  padding: 0 20px;
  min-width: 1200px;
  position: sticky;
  top: 0;
  height: 60px;
  color: #fff;
  font-family: "font-a";
  .cont {
    display: flex;
    margin: auto;
    height: 100%;
    justify-content: space-between;
    .logo {
      display: flex;
      padding-left: 10px;
      align-items: center;
      width: 200px;
      height: 100%;
      cursor: pointer;
      img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
      }
      .name {
        font-size: 28px;
        margin-left: 10px;
      }
    }
    .nav {
      display: flex;
      height: 100%;
      align-items: center;
      font-size: 22px;
      .col {
        display: flex;
        align-items: center;
        padding: 0 20px;
        height: 100%;
        &:hover {
          background-color: #56c8fd79;
          cursor: pointer;
        }
      }
    }
  }
}
</style>
