<template>
    <div class="markdown-body" v-html="markdown" ref="markdown"></div>
</template>

<script>
import 'github-markdown-css'
export default {
  name: 'Markdown',
  props: {
    markdown: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      info: null
    }
  },

  mounted () {
    setTimeout(() => {
      this.$nextTick(this.getInfo())
    }, 1)
  },

  methods: {
    getInfo () {
      const isInfo = this.$refs.markdown.children[0].id === 'info'
      if (!isInfo) return
      const info = this.$refs.markdown.children[1].innerText
      this.info = JSON.parse(info)
    }
  }
}
</script>

<style lang="less" scoped>
.markdown-body {
    padding: 10px;
    /deep/#info {
        display: none;
        &+p {
            display: none;
        }
    }
}
</style>
