<template>
  <div class="article-list">
    <div
      class="item"
      v-for="(item, index) in dataList"
      :key="index"
      @click="viewArticle(item)"
    >
      <img src="https://s1.ax1x.com/2022/10/17/xDnWff.jpg" alt="" />
      <div class="text">{{ item.title }}</div>
    </div>
  </div>
</template>

<script>
import mdArr from '@/assets/article'
export default {
  name: 'ArticleList',

  data () {
    return {
      dataList: []
    }
  },

  mounted () {
    this.dataList = mdArr
  },

  methods: {
    viewArticle (item) {
      this.$router.push({
        path: '/article',
        query: { id: item.title }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.article-list {
  display: flex;
  padding: 0 12px;
  flex-wrap: wrap;

  .item {
    margin: 0 12px 24px;
    width: 270px;
    height: 210px;
    background-color: #fff;
    overflow: hidden;
    border-radius: 5px;
    cursor: pointer;
    img {
      display: block;
      width: 100%;
      height: 150px;
    }
    .text {
      box-sizing: border-box;
      margin-top: 10px;
      padding: 0 10px;
      line-height: 1.3;
      word-break: break-all;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2; /* 这里是超出几行省略 */
      overflow: hidden;
    }
    &:hover {
      transform: scale(1.03);
      transition: all 0.3s;
    }
  }
}
</style>
